.user-page {
  .user-nav {
    display: flex;
    padding-top: 10px;
    *{
      margin-right: 5px;
    }
  }

  a {
    color: black;
  }
  height: 100%;
  padding: 20px 0;
  .logo {
    height: 30px;
    position: absolute;
    top: 20px;
    left: 22px;
  }

  .stats-btn {
    text-align: center;
    margin-top: 44px;
  }

  .play-icons {
    justify-content: center;
    margin-top: 70px;

    * {
      margin: 0 10px;
    }

    div .play-icons-text {
      margin-bottom: 20px;
    }
  }

  .logout-btn {
    position: absolute;
    top: 15px;
    right: 8px;
  }
}
